import React from 'react';
import TextField from 'material-ui/TextField';
import Card from 'material-ui/Card';
import RaisedButton from 'material-ui/RaisedButton';



export default class EmployeeDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

      items: [],
      name: '',
      bloodgroup: '',
      companyid: '',
      address: '',

      selectEmp: ""
    };

    this.handleSubmit2 = this.handleSubmit2.bind(this);
    this.setSelectedEmployee = this.setSelectedEmployee.bind(this);
    this.handle = this.handle.bind(this);
    this.handlesave = this.handlesave.bind(this);


  }
  setSelectedEmployee(item) {

    this.setState({
      selectEmp: item
    });


  }

  handle() {

    let n = this.state.items.length + 1;

    let emp = { "id": n, "name": '', "bloodgroup": '', "companyid": '', "address": '' }

    this.state.items.push(emp)

    this.setState({ selectEmp: emp })


    console.log(JSON.stringify(this.state.items));
  }




  RouteApp2
  handleSubmit2(emp) {


    let empList = this.state.items;

    let e = empList.find(c => c.id == emp.id);


    if (e == null) {
      this.state.items.push(emp)
    } else {
      e.name = emp.name;
      e.bloodgroup = emp.bloodgroup;
      e.companyid = emp.companyid;
      e.address = emp.address;
    }


    this.setState({
      items: empList,

      name: emp.name,
      bloodgroup: emp.bloodgroup,
      companyid: emp.companyid,
      address: emp.address,

      selectEmp: ""

    });

    console.log("name", this.state.name);


  }
  handlesave() {

    fetch('http://localhost:8080/RestApp/rest/employee/create', {

      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({

        " name": this.state.name,
        "bloodgroup": this.state.bloodgroup,
        "companyID": this.state.companyid,
        "address": this.state.address


      })
    })
  }

  render() {

    return (
      <div className="table">
        <div style={{ backgroundColor: "#1E90FF", padding: "20px" }}>
        </div>
        <div style={{ backgroundColor: "#87CEFA", padding: "10px", textAlign: "center" }}>
          <h1>Employees</h1>
        </div>
        <br></br>
        <br></br>
        <br></br>
        <div className="container-fiuld">
          <div className="row">

            <div className="col-sm-1"></div>

            <div id="abc" className="col-sm-4" >
              <Card>
                <fieldset>
                  <legend><h4 className="list-group-item disabled">List Of Employees</h4></legend>
                  <br></br>
                  <div className="col-sm-1"></div>
                  <div className="col-sm-9" >
                    <form>

                      <EmployeeList empList={this.state.items} setSelectedEmployee={this.setSelectedEmployee} />

                    </form>
                  </div>
                  <div className="col-sm-2"></div>
                </fieldset>
                <br></br>
                <fieldset>

                  <fieldset>
                    <legend></legend>
                    <div className="col-sm-1"></div>
                    <a href="#" onClick="">
                      <span className="glyphicon glyphicon-user" onClick={this.handle}></span>
                    </a>

                  </fieldset>

                </fieldset>


                <br></br>
              </Card>
            </div>


            <div className="col-sm-1">
            </div>

            <div id="xyz" className="col-sm-4" >
              <Card>

                <fieldset style={{}}>
                  <legend><h4 className="list-group-item disabled">Add Employee Details</h4></legend>
                  <br></br>
                  <div className="row">
                    <div className="col-sm-1"></div>
                    <div className="col-sm-10">
                      <form >

                        <EmpDetails selectedemp={this.state.selectEmp} handleSubmit2={this.handleSubmit2} />

                      </form>
                    </div>
                  </div>

                  <br></br>
                  <br></br>

                </fieldset>
              </Card >
            </div>
          </div>

          <br></br>
          <br></br>
          <br></br>
         
          <div className="row">
            <div className="col-sm-1"></div>
            <div className="col-sm-1">
            {/* <button type="button" className="btn" onClick="" >Cancel</button>*/}
            </div>
            <div className="col-sm-8">
            </div>
            <div className="col-sm-1">
              <button type="button" className="btn" onClick={this.handlesave} style={{ backgroundColor: "DodgerBlue " }}>  Save  </button>
            </div>
          </div>
        </div>

      </div>
    );
  }

}

class EmployeeList extends React.Component {

  render() {

    return (

      <form>{

        this.props.empList.map((item) => {
          return (

            <a href="#" key={item.id} className="list-group-item " onClick={() => this.props.setSelectedEmployee(item)}>{item.name}</a>

          )
        })
      }
      </form>
    )
  }

}


class EmpDetails extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      selectedEmployee: props.selectedemp,

    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handlereset = this.handlereset.bind(this)

  }


  componentWillReceiveProps(nextProps) {

    if (nextProps.selectedemp == "") {

      this.handlereset()

    }
    else {
      this.setState({
        selectedEmployee: nextProps.selectedemp
      })
    }
    console.log("selected" + JSON.stringify(this.state.selectedEmployee));
  }

  handleChange(event) {


    this.emp = { [event.target.name]: event.target.value }


    this.setState((prevState, props) => {
      console.log();
      return {

        selectedEmployee: Object.assign({}, prevState.selectedEmployee, this.emp)
      }
    })


  } handleSubmit(event) {

    let emplist = this.state.selectedEmployee;


    if (emplist.id == null) {

      this.handlereset();

    } else {

      this.props.handleSubmit2(this.state.selectedEmployee);
    }
  }
  handlereset() {


    let emp = { "name": '', "bloodgroup": '', "companyid": '', "address": '' }

    this.setState({ selectedEmployee: emp })

  }



  render() {



    return (
      <form>
        <TextField
          //hintText="Enter Name"
          floatingLabelText="Name"
          floatingLabelFixed={true}
          name="name"
          id="name"
          onChange={this.handleChange}
          value={this.state.selectedEmployee.name}
        />

        <br></br>
        <TextField
          //hintText="Enter bloodgroup"
          floatingLabelText="Blood Group"
          floatingLabelFixed={true}
          name="bloodgroup"
          onChange={this.handleChange}
          value={this.state.selectedEmployee.bloodgroup}
        />
        <br></br>
        <TextField
          //hintText="company id"
          floatingLabelText="Company ID"
          floatingLabelFixed={true}
          name="companyid"
          onChange={this.handleChange}
          value={this.state.selectedEmployee.companyid}
        />
        <br></br>
        <TextField
          // hintText="enter Address"
          floatingLabelText="Enter Address"
          floatingLabelFixed={true}
          name="address"
          onChange={this.handleChange}
          value={this.state.selectedEmployee.address}
        />


        <br></br>
        <br></br>
        <br></br>

        <div className="col-sm-2">
          <RaisedButton label="Reset" onClick={this.handlereset} />
        </div>
        <div className="col-sm-5">
        </div>
        <div className="col-sm-2">
          <RaisedButton label="ADD" primary={true} onClick={this.handleSubmit} />
        </div>
        <br></br>

      </form>

    )
  }
}




