import React from 'react';

import ReactDOM from 'react-dom';

import data from './Data.js';

import { HashRouter, Route, Router, Link, Switch,Redirect } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import TextField from 'material-ui/TextField';
import Card from 'material-ui/Card';
import RaisedButton from 'material-ui/RaisedButton';


export default class RoutingExample extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            contacts: data.contacts

        }
    }


    render() {

        return (

            <div>

                <Header />

                <Main />

            </div>

        );

    }

}



class Header extends React.Component {




    render() {

        return (

            <div className="container-fluid">

                <div className="row">

                    <div className="col-sm-12">



                        <ul className="breadcrumb">

                            <li><NavLink to="/" activeStyle={{
                                fontWeight: 'bold',
                            }}>React Routing</NavLink></li>

                            <li><NavLink to="/" activeStyle={{
                                fontWeight: 'bold',
                            }}>Home</NavLink></li>

                            <li><NavLink to="/list" activeStyle={{
                                fontWeight: 'bold',

                            }}>List of Employees</NavLink></li>

                            <li><NavLink to="/add" activeStyle={{
                                fontWeight: 'bold',

                            }}>Add Employees</NavLink></li>

                            <li><Link to="/addemp" >Add Employees</Link></li>

                        </ul>

                    </div>


                    <div>
                    </div>
                </div>
            </div>

        );

    }

}
class Main extends React.Component {

    constructor(props) {
        super(props);
        this.state = {

            name: 'srinath'


        };
        
    }
    render() {

        return (
            <div>

                <Route exact path="/" component={Home} />

                <Route path="/list" component={ContactList} />

                 <Redirect from="/addemp" to="/add"/>

                <Route path="/add" component={EmpDetails} name={this.state.name} />

            </div>

        );

    }

}
class Home extends React.Component {

    render() {

        return (

            <div>

                <h1>React routing home page</h1>

            </div>

        )

    }

}

class ContactList extends React.Component {

    render() {

        return (

            <div className="list-group">

                {

                    data.contacts.map((item) => {

                        return (

                            <NavLink activeclassName="selected" activeStyle={{
                                fontWeight: 'bold',
                                color: 'blue'
                            }} to={`/contacts/${item.id}`} key={item.id} className="list-group-item">{item.name}</NavLink>

                        );

                    })

                }

            </div>

        )

    }

}
class EmpDetails extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
           
             name: 'srinath'

        }
    }
    handleSubmit(){
        <Route path="/add" component={ContactList} />
    }



       


        render() {

            return (


                <div className="container-fluid">

                    <h1>add emp</h1>
                    <div className="row">
                        <div className="col-sm-2"></div>
                        <div className="col-sm-6">
                            <card>
                                <fieldset>
                                    <form>

                                        <TextField
                                            //hintText="Enter Name"
                                            floatingLabelText="Name"
                                            floatingLabelFixed={true}
                                            name="name"
                                            id="name"
                                            onChange={this.handleChange}
                                            value={this.props.location.name}
                                        />

                                        <br></br>
                                        <TextField
                                            //hintText="Enter bloodgroup"
                                            floatingLabelText="Blood Group"
                                            floatingLabelFixed={true}
                                            name="bloodgroup"
                                            onChange={this.handleChange}
                                        //value={this.state.selectedEmployee.bloodgroup}
                                        />
                                        <br></br>
                                        <TextField
                                            //hintText="company id"
                                            floatingLabelText="Company ID"
                                            floatingLabelFixed={true}
                                            name="companyid"
                                            onChange={this.handleChange}
                                        //value={this.state.selectedEmployee.companyid}
                                        />
                                        <br></br>
                                        <TextField
                                            // hintText="enter Address"
                                            floatingLabelText="Enter Address"
                                            floatingLabelFixed={true}
                                            name="address"
                                            onChange={this.handleChange}
                                        // value={this.state.selectedEmployee.address}
                                        />

                                    </form>
                                </fieldset>
                            </card>



                            <br></br>
                            <br></br>
                            <br></br>
                            <RaisedButton label="Reset" onClick={this.handlereset} />

                            <div className="col-sm-4">
                                <RaisedButton label="ADD" primary={true} onClick={this.handleSubmit} />
                            </div>
                        </div><Route path="/list" component={ContactList} />






                        <br></br>


                    </div>
                </div>
            )
        }
    }



